* {
  font-family: Helvetica;
  text-align: center;
  margin: 5px;
  background-color: #19c3fb;
}

h1 {
  font-size: 55px;
}

button {
  background-color: #1d71f2;
  color: #fff;
  padding: 10px 20px;
  border-radius: 10px;
  border-width: 2px;
}

input {
  padding: 10px 10px;
  border-radius: 10px;
  background-color: #fff;
  border-width: 2px;
}

label {
  font-weight: bold;
}

select {
  padding: 10px;
  border-width: 2px;
  border-radius: 10px;
  background-color: #fff;
}

.dataInfo {
  padding: 30px;
  display: grid;
  grid-template-columns: auto auto;
}
